import "./styles.css";
import menu from "./output";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < 4; i++) {
    items.push(
      <li>
        <b>{keys[i]}</b>:{values[i]}
      </li>
    );
  }

  return <ul>{items}</ul>;
}

